// eslint-disable-next-line import/no-named-default
import type { Dayjs, default as dayjs } from "dayjs";

import type { Locale } from "./locale.js";

const monthFormat =
  "___________".split(
    "_",
  );
const monthStandalone =
  "___________".split(
    "_",
  );

const MONTHS_IN_FORMAT = /D[oD]?(\[[^[\]]*\]|\s)+MMMM?/;

const plural = (word: string, num: number): string => {
  const forms = word.split("_");

  return num % 10 === 1 && num % 100 !== 11
    ? forms[0]
    : num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20)
    ? forms[1]
    : forms[2]; // eslint-disable-line
};

const relativeTimeWithPlural = (
  number: number,
  withoutSuffix: boolean,
  key: "m" | "h" | "ss" | "mm" | "hh" | "dd" | "MM" | "yy",
): string => {
  const format = {
    ss: withoutSuffix ? "__" : "__",
    mm: withoutSuffix ? "__" : "__",
    hh: withoutSuffix ? "__" : "__",
    dd: "__",
    MM: "__",
    yy: "__",
  };

  if (key === "m") return withoutSuffix ? "" : "";
  if (key === "h") return withoutSuffix ? "" : "";

  return `${number} ${plural(format[key], +number)}`;
};

const months = (dayjs: Dayjs, format: string): string => {
  if (MONTHS_IN_FORMAT.test(format)) return monthFormat[dayjs.month()];

  return monthStandalone[dayjs.month()];
};

months.s = monthStandalone;
months.f = monthFormat;

const locale: Partial<Locale> = {
  name: "uk",
  weekdays: "______".split(
    "_",
  ),
  weekdaysShort: "______".split("_"),
  weekdaysMin: "______".split("_"),
  // @ts-ignore
  months,
  monthsShort: "___________".split(
    "_",
  ),
  weekStart: 1,
  relativeTime: {
    future: " %s",
    past: "%s ",
    s: " ",
    // @ts-ignore
    m: relativeTimeWithPlural,
    // @ts-ignore
    mm: relativeTimeWithPlural,
    // @ts-ignore
    h: relativeTimeWithPlural,
    // @ts-ignore
    hh: relativeTimeWithPlural,
    d: "",
    // @ts-ignore
    dd: relativeTimeWithPlural,
    M: "",
    // @ts-ignore
    MM: relativeTimeWithPlural,
    y: "",
    // @ts-ignore
    yy: relativeTimeWithPlural,
  },
  ordinal: (n) => n,
  formats: {
    LT: "HH:mm",
    LTS: "HH:mm:ss",
    L: "DD.MM.YYYY",
    LL: "D MMMM YYYY .",
    LLL: "D MMMM YYYY ., HH:mm",
    LLLL: "dddd, D MMMM YYYY ., HH:mm",
  },
};

export const loadUkLocale = (extendedDayjs: typeof dayjs): void => {
  extendedDayjs.locale("uk", locale);
};
