// Vietnamese [vi]
import type dayjs from "dayjs";

import type { Locale } from "./locale.js";

const locale: Partial<Locale> = {
  name: "vi",
  weekdays: "ch nht_th hai_th ba_th t_th nm_th su_th by".split("_"),
  months:
    "thng 1_thng 2_thng 3_thng 4_thng 5_thng 6_thng 7_thng 8_thng 9_thng 10_thng 11_thng 12".split(
      "_",
    ),
  weekStart: 1,
  weekdaysShort: "CN_T2_T3_T4_T5_T6_T7".split("_"),
  monthsShort:
    "Th01_Th02_Th03_Th04_Th05_Th06_Th07_Th08_Th09_Th10_Th11_Th12".split("_"),
  weekdaysMin: "CN_T2_T3_T4_T5_T6_T7".split("_"),
  ordinal: (n) => n,
  formats: {
    LT: "HH:mm",
    LTS: "HH:mm:ss",
    L: "DD/MM/YYYY",
    LL: "D MMMM [nm] YYYY",
    LLL: "D MMMM [nm] YYYY HH:mm",
    LLLL: "dddd, D MMMM [nm] YYYY HH:mm",
    l: "DD/M/YYYY",
    ll: "D MMM YYYY",
    lll: "D MMM YYYY HH:mm",
    llll: "ddd, D MMM YYYY HH:mm",
  },
  relativeTime: {
    future: "%s ti",
    past: "%s trc",
    s: "vi giy",
    m: "mt pht",
    mm: "%d pht",
    h: "mt gi",
    hh: "%d gi",
    d: "mt ngy",
    dd: "%d ngy",
    M: "mt thng",
    MM: "%d thng",
    y: "mt nm",
    yy: "%d nm",
  },
};

export const loadViLocale = (extendedDayjs: typeof dayjs): void => {
  extendedDayjs.locale("vi", locale);
};
