// Turkish [tr]
import type dayjs from "dayjs";

import type { Locale } from "./locale.js";

const locale: Partial<Locale> = {
  name: "tr",
  weekdays: "Pazar_Pazartesi_Sal_aramba_Perembe_Cuma_Cumartesi".split("_"),
  weekdaysShort: "Paz_Pzt_Sal_ar_Per_Cum_Cmt".split("_"),
  months:
    "Ocak_ubat_Mart_Nisan_Mays_Haziran_Temmuz_Austos_Eyll_Ekim_Kasm_Aralk".split(
      "_",
    ),
  monthsShort: "Oca_ub_Mar_Nis_May_Haz_Tem_Au_Eyl_Eki_Kas_Ara".split("_"),
  ordinal: (n) => `${n}.`,
  formats: {
    LTS: "HH:mm:ss",
    LT: "HH:mm",
    L: "DD.MM.YYYY",
    LL: "D MMMM YYYY",
    LLL: "D MMMM YYYY HH:mm",
    LLLL: "D MMMM YYYY HH:mm dddd",
  },
};

export const loadTrLocale = (extendedDayjs: typeof dayjs): void => {
  extendedDayjs.locale("tr", locale);
};
